<template>
  <el-container>
    <el-aside :width="width" style="transition: 0.3s">
      <Menu :collapse="collapse"></Menu>
    </el-aside>
    <el-container>
      <el-header>
        <div class="control">
          <i :class="['collapse', icon]" @click="changeFn"></i>
          <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: subTitle.path }">
              {{subTitle.title}}</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class="userInfo">
          <el-dropdown>
            <span class="el-dropdown-link">
              <el-avatar size="small" :src="src" style="margin-right: 10px;"
                         fit="cover" icon="el-icon-user-solid"></el-avatar>
              {{userName}}
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-show="false">
                <i class="el-icon-switch-button"/>
                退出登录
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-header>
      <el-main :class="[{'special': $route.meta.special}]">
        <keep-alive>
          <router-view v-if="$route.meta.keepAlive"></router-view>
        </keep-alive>
        <router-view v-if="!$route.meta.keepAlive"></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import Menu from './components/menu.vue';

export default {
  components: {
    Menu,
  },
  watch: {
    collapse(val) {
      if (val) {
        this.icon = 'el-icon-s-unfold';
        this.width = '64px';
      } else {
        this.icon = 'el-icon-s-fold';
        this.width = '256px';
      }
    },
  },
  computed: {
    subTitle() {
      const { path, meta } = this.$route && this.$route.matched[1];
      return { path: path || '', title: meta.title || '', parent: meta.parent };
    },
    collapse() {
      return this.$store.state.collapse;
    },
  },
  data() {
    return {
      userName: 'admin',
      icon: 'el-icon-s-unfold',
      width: '64px',
      src: '',
    };
  },
  methods: {
    changeFn() {
      this.$store.dispatch('actionChange');
    },
  },
};
</script>

<style scoped lang="less">
  .el-container {
    height: 100%;
  }

  .el-header {
    background-color: #fff;
    color: #5a5a5a;
    padding: 0;
    height: 64px !important;
    line-height: 64px;
    display: flex;
    justify-content: space-between;
    .control {
      display: flex;
      .el-breadcrumb {
        line-height: inherit;
      }
      .collapse {
        line-height: inherit;
        color: rgba(0,0,0,.65);
        font-size: 18px;
        padding: 0 24px;
        cursor: pointer;
        transition: color .3s;
      }
    }
    .userInfo {
      padding: 0 15px;
      cursor: pointer;
      display: flex;
      align-items: center;
      button {
        margin-right: 15px;
      }
      .el-dropdown-link {
        padding: 0 15px;
        display: flex;
        align-items: center;
        &:hover {
          background: rgba(0, 0, 0, .025);
        }
      }
    }
  }

  .el-main {
    background: #fff;
    padding: 24px;
    margin: 16px;
  }

  .special {
    padding: 0;
    margin: 0;
    background: #f0f2f5;
    & > div {
      height: 100%;
    }
  }

  .el-aside {
    height: 100%;
    background: rgb(0, 21, 41);
  }
</style>
